@charset "UTF-8";

@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

html {
  font-family: sans-serif;
  line-height: 1.2;
}

body {
  margin: 0;
  font-size: 1rem;
  color: #000000;
  font-weight: 400;
  font-family: "PT Serif Pro";
  text-align: left;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 1170px;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 179px;
  height: 51px;
  border-radius: 63% 8% 44% 10%/86% 23% 88% 24%;
  background: #da1f8d;
  box-shadow: 0 26px 46px rgba(0, 0, 0, 0.24), 0 3px 0 #952165;
  font-family: Roboto;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
}

.site-header {
  display: flex;
  align-items: center;
  margin: 0 auto;
  max-width: 1199px;
  width: calc(100% - 30px);
}

.sh-logo {
  display: block;
  width: 191px;
}

.sh-menu {
  margin: 0 7.5px;
  color: #000000;
  font-family: Montserrat;
  font-size: 14px;
  font-weight: 400;
  /* Text style for "Портфолио" */
  display: flex;
  align-items: center;
}

.sh-menu__link:hover {
  color: #da1f8d;
}

.shc-work-time {
  font-family: Montserrat;
  color: #000000;
  font-size: 12px;
  font-weight: 500;
}

.shc-work-phone {
  font-family: "Montserrat - Semi Bold";
  color: #000000;
  font-size: 26px;
  font-weight: 400;
}

.container {
  width: 320px;
  max-width: calc(100% - 30px);
  margin: 0 auto;
}

@media screen and (min-width: 1200) {
  .container {
    width: 1140px;
  }
}

@media only screen and (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (max-width: 991px) {
  .container {
    max-width: 720px;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    max-width: 320px;
  }
}

@media screen and (min-width: 768px) and (max-width: 991) {
  .container {
    width: 720px;
  }
}

@media screen and (min-width: 992px) and (max-width: 1199) {
  .container {
    width: 960px;
  }
}